'use client';

import { useState } from 'react';
import { TopNav } from '../components/TopNav';
import { CreateQuestCard } from '../components/CreateQuestCard';
import { QuestsList } from '../components/QuestsList';
import { AuthForm } from '../components/AuthForm';
import { AuthProvider, useAuth } from '../contexts/AuthContext';
import { Loader2 } from 'lucide-react';

function HomePage() {
  const { user, loading } = useAuth();
  const [refreshTrigger, setRefreshTrigger] = useState(0);

  const handleQuestCreated = () => {
    setRefreshTrigger(prev => prev + 1);
  };

  const handleAuthSuccess = () => {
    // The auth context will automatically update the user state
    // No need to do anything here
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-slate-100/80 flex items-center justify-center">
        <div className="text-center">
          <Loader2 className="w-8 h-8 animate-spin mx-auto mb-4 text-blue-500" />
          <p className="text-gray-600">Loading...</p>
        </div>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="min-h-screen bg-slate-100/80">
        {/* Sticky Navigation */}
        <TopNav />
        
        {/* Auth Form */}
        <main className="mx-auto max-w-md px-4 py-8">
          <AuthForm onAuthSuccess={handleAuthSuccess} />
        </main>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-slate-100/80">
      {/* Sticky Navigation */}
      <TopNav />
      
      {/* Main Content */}
      <main className="mx-auto max-w-6xl px-4 py-8">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Create Quest Card */}
          <div>
            <CreateQuestCard onQuestCreated={handleQuestCreated} />
          </div>
          
          {/* Quests List */}
          <div>
            <QuestsList refreshTrigger={refreshTrigger} />
          </div>
        </div>
      </main>
    </div>
  );
}

export default function Home() {
  return (
    <AuthProvider>
      <HomePage />
    </AuthProvider>
  );
}
